<template>
    <el-dialog
    width="600px"
    title="新增用户"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
    >
       <el-form :inline="true" ref="userForm" :model="userForm" :rules="formRules" label-width="80px">
           <el-row>
               <el-col :span="12">
                    <el-form-item label="用户名" prop="userAccount">
                        <el-input v-model="userForm.userAccount"  maxlength="50" />
                    </el-form-item>
               </el-col>
               <el-col :span="12">
                    <el-form-item label="密码" prop="userPassword">
                        <el-input type="password" v-model="userForm.userPassword"  />
                    </el-form-item>
               </el-col>
           </el-row>
           <el-row>
               <el-col :span="12">
                    <el-form-item label="真实姓名" prop="userRealName">
                        <el-input v-model="userForm.userRealName" maxlength="50"/>
                    </el-form-item>
               </el-col>
               <el-col :span="12">
                    <el-form-item label="性别" prop="userGender">
                        <el-radio-group v-model="userForm.userGender">
                            <el-radio label="1">男</el-radio>
                            <el-radio label="0">女</el-radio>
                        </el-radio-group>
                    </el-form-item>
               </el-col>
           </el-row>
           <el-row>
               <el-col :span="12">
                    <el-form-item label="机构" prop="userOrgUuid">
                        <!--<el-select v-model="userForm.userOrgUuid" style="width:178px">-->
                            <!--<el-option-->
                            <!--v-for="(org,index) in org"-->
                            <!--:key="index"-->
                            <!--:label="org.orgName"-->
                            <!--:value="org.orgUuid"-->
                            <!--/>-->
                        <!--</el-select>-->
                      <el-cascader
                        style="width:178px"
                        :options="options"
                        change-on-select
                        v-model="userForm.userOrgUuid"
                      ></el-cascader>
                    </el-form-item>
               </el-col>
               <el-col :span="12">
                    <el-form-item label="状态" prop="userEnable">
                        <el-radio-group v-model="userForm.userEnable">
                            <el-radio label="1">启用</el-radio>
                            <el-radio label="0">禁用</el-radio>
                        </el-radio-group>
                    </el-form-item>
               </el-col>
           </el-row>
           <el-row>
               <el-col :span="12">
                    <el-form-item label="座机" prop="userTelephone">
                        <el-input v-model="userForm.userTelephone" placeholder="座机:区号-号码" />
                    </el-form-item>
               </el-col>
               <el-col :span="12">
                    <el-form-item label="手机" prop="userMobile">
                        <el-input v-model="userForm.userMobile" />
                    </el-form-item>
               </el-col>
           </el-row>
            <el-row>
                <el-col :span="6" :offset="18">
                    <el-button type="primary" @click="save">保存</el-button>
                    <el-button @click="cancel">取消</el-button>
                </el-col>
            </el-row>
       </el-form>
    </el-dialog>
</template>
<script>
/**
 * User Add Component
 * ----------------------
 * Author: zhou qi
 * Date: 2018.03.20
 */
// validate
import Validator from '@/utils/extendValidate/extendValidate'

// api
import { createUser } from '@/api/user'

export default {
  name: 'UserEdit',
  data () {
    return {
      isVisible: false,
      orgnization: [],
      userForm: {
        userAccount: '',
        userPassword: '',
        userRealName: '',
        userGender: '1',
        userOrgUuid: [],
        userEnable: '1',
        userTelephone: '',
        userMobile: ''
      },
      formRules: {
        userAccount: [{required: true, message: '请输入账号', trigger: 'blur'},
          {validator: Validator.account, trigger: 'blur'}],
        userRealName: [{required: true, message: '请输入姓名', trigger: 'blur'}],
        userPassword: [{required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, message: '密码最小长度6位', trigger: 'blur'},
          {max: 18, message: '密码最大长度18位', trigger: 'change'},
          {validator: Validator.simplePassword, trigger: 'blur'}],
        userOrgUuid: [{required: true, message: '请选择机构', trigger: 'blur'}],
        userMobile: [{validator: Validator.cellPhone, trigger: 'blur'}],
        userTelephone: [{validator: Validator.isTelephone, trigger: 'blur'}]
      },
      options: []
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    org: {
      type: Array,
      required: true
    }
  },
  watch: {
    show (isShow) {
      this.isVisible = isShow
    },
    org (org) {
      this.options = org
    }
  },
  methods: {
    save () {
      this.$refs.userForm.validate(isValid => {
        if (isValid) {
          const params = Object.assign({}, this.userForm)
          params.userOrgUuid = params.userOrgUuid[params.userOrgUuid.length-1]
          console.log(this.options,params.userOrgUuid)
          createUser(params).then(res => {
            if (!res.error) {
              this.$message({
                message: '添加用户成功!',
                type: 'success'
              })
              this.cancel()
              this.$emit('updateList')
            }
          })
        }
      })
    },
    cancel () {
      this.$refs.userForm.resetFields()
      this.$emit('update:show', false)
      this.isVisible = false
    }
  }
}
</script>



