<template>
     <el-form :model="searchForm" ref="searchForm"  label-width="80px" >
            <el-row>
                <el-col :span="6">
                    <el-form-item label="账号"  prop="userAccount">
                      <el-input v-model="searchForm.userAccount"  clearable ></el-input>
                    </el-form-item>
                </el-col>
                  <!--<el-col :span="6">-->
                    <!--<el-form-item label="仓库" prop="userOrgId">-->
                      <!--<el-select v-model="searchForm.userOrgId" >-->
                          <!--<el-option-->
                            <!--v-for="(org, index) in org"-->
                            <!--:key="index"-->
                            <!--:label="org.orgName"-->
                            <!--:value="org.orgUuid"-->
                          <!--/>-->

                      <!--</el-select>-->
                    <!--</el-form-item>-->
                <!--</el-col>-->
                <el-col :span="6">
                  <el-form-item label="所属机构" prop="userOrgUuid">
                    <el-select v-model="searchForm.userOrgUuid" clearable filterable style="width:178px">
                      <el-option
                        v-for="(item,index) in org"
                        :key="index"
                        :label="item.orgName"
                        :value="item.id"
                      />
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6" class="flex-right">
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-col>
            </el-row>
          </el-form>
</template>

<script>
/**
 * User Search Component
 * ----------------------
 * Author: zhou qi
 * Date: 2018.03.20
 */
export default {
  name: 'UserManagementSearch',
  data () {
    return {
      // 检索界面数据
      orgList: [],
      searchForm: {
        userAccount: '',
        userOrgUuid: '',
        userEnable: ''
      }
    }
  },
  props: {
    org: {
      type: Array,
      required: true
    }
  },
  mounted () {
    this.search()
  },
  methods: {
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 关闭查询框
    closeSearch: function (formName) {
      this.$emit('closeSearch')
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



